


























































.time-date {
  display: flex;
  flex-direction: column;
  text-align: center;
  .time {
    flex: 9;
    font-size: 4.5rem;
    line-height: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  .date  {
    flex: 1;
    font-size: .6rem;
    line-height: 1;
    white-space: nowrap;
  }
}
