
































































































































































































.weathers {
  display: flex;
  flex-direction: column;
  position: relative;

  .today-weather {
    flex: 6;
  }

  .future-weathers {
    flex: 4;
    display: flex;
    justify-content: space-between;
  }

  /deep/ .mu-loading-wrap {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
