








































.today-weather {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  .max-min-temp {
    font-size: 0.5rem;
    text-align: right;
  }
  .current-weather {
    display: flex;
    justify-content: center;
    align-items: center;
    .weather {
      height: 3rem;
    }
    .temprature {
      font-size: 2.6rem;
      line-height: 3rem;
    }
    .unit {
      font-size: 1.6rem;
      line-height: 2.4rem;
      align-self: baseline;
    }
  }
  .others {
    font-size: 0.5rem;
    text-align: center;
    display: flex;
    justify-content: space-evenly;
  }
}
